import React, { useState, useEffect } from 'react';
import './App.css';
import SignUpPage from './components/SignUpPage'
import { initialize, Event } from '@harnessio/ff-javascript-client-sdk'
import uuid from 'react-uuid'
import ReactGA from 'react-ga';

function App() {
    const TRACKING_ID = "G-WSFBSR33NK"; // OUR_TRACKING_ID
    ReactGA.initialize(TRACKING_ID);
    const targetIdent = uuid();
    const name = (Math.random() + 1).toString(36).substring(7);
    const [featureFlags, setFeatureFlags] = useState({})

    useEffect(() => {
        console.log("targetID", targetIdent, ". name", name)
        const cf = initialize('15895585-8fa3-460f-8c53-e63a2ace4fb3', {
            identifier: targetIdent,
            name: targetIdent,
            attributes: {
                username: name
            }
        });

        cf.on(Event.READY, flags => {
            setFeatureFlags(flags)
        })

        cf.on(Event.CHANGED, flagInfo => {
            if (flagInfo.deleted) {
                setFeatureFlags(currentFeatureFlags => {
                    delete currentFeatureFlags[flagInfo.flag]
                    return { ...currentFeatureFlags }
                })
            } else {
                setFeatureFlags(currentFeatureFlags => ({ ...currentFeatureFlags, [flagInfo.flag]: flagInfo.value }))
            }
        })

        //return cf.close()
    }, [])

  return (
    <div className="App">
        <SignUpPage featureFlags={featureFlags}/>
    </div>
  );
}

export default App;
