import * as React from 'react';
import CssBaseline from '@mui/material/CssBaseline';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Toolbar from '@mui/material/Toolbar';
import Paper from '@mui/material/Paper';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import SignUpFormA from "./SignUpFormA";
import SignUpFormB from "./SignUpFormB";
import ReactGA from "react-ga";

const theme = createTheme();
const signUpFlagID = "UI_SIGNUP_PAGE"

export default function Checkout({ featureFlags }) {
    const [complete, setComplete] = React.useState(false);

    const signUp = () => {
        setComplete(true)
        const category = "news letter"
        const eventTracker = (action = "sign up", label = featureFlags[signUpFlagID]) => {
            ReactGA.event({category, action, label});
        }
        eventTracker("call")
    }

    const renderForm = () => {
        console.log("renderForm", featureFlags)
        if (signUpFlagID in featureFlags && featureFlags[signUpFlagID] !== "default") {
            if (featureFlags[signUpFlagID] === "page_a") {
                return (
                    <SignUpFormA />
                )
            } else {
                return (
                    <SignUpFormB />
                )
            }
        } else {
            return (
                <CircularProgress color="inherit" />
            )
        }

    }

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline />
            <AppBar
                position="absolute"
                color="default"
                elevation={0}
                sx={{
                    position: 'relative',
                    borderBottom: (t) => `1px solid ${t.palette.divider}`,
                }}
            >
                <Toolbar>
                    <Typography variant="h6" color="inherit" noWrap>
                        Feature Flags Analytics
                    </Typography>
                </Toolbar>
            </AppBar>
            <Container component="main" maxWidth="sm" sx={{ mb: 4 }}>
                <Paper variant="outlined" sx={{ my: { xs: 3, md: 6 }, p: { xs: 2, md: 3 } }}>
                    <Typography component="h1" variant="h4" align="center">
                        Sign up to our new letter!
                    </Typography>
                    <React.Fragment>
                        {complete ? (
                            <React.Fragment>
                                <Typography variant="h5" gutterBottom>
                                    Thank you signing up!
                                </Typography>
                            </React.Fragment>
                        ) : (
                            <React.Fragment>
                                {renderForm()}
                                <Box sx={{ display: 'flex', justifyContent: 'flex-end' }}>
                                    <Button
                                        variant="contained"
                                        onClick={() => {signUp()}}
                                        sx={{ mt: 3, ml: 1 }}
                                    >
                                        Sign up!
                                    </Button>
                                </Box>
                            </React.Fragment>
                        )}
                    </React.Fragment>
                </Paper>
            </Container>
        </ThemeProvider>
    );
}